import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { HttpClientModule } from '@angular/common/http';
import { AngularFireModule } from 'angularfire2';
import { AngularFireDatabaseModule } from 'angularfire2/database';

import { SQLite } from '@ionic-native/sqlite';
import { Sim } from '@ionic-native/sim';
import { IonicStorageModule } from '@ionic/storage';
import { Network } from '@ionic-native/network';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { Geolocation } from '@ionic-native/geolocation';
import { GoogleMaps } from '@ionic-native/google-maps';

import { MyApp } from './app.component';
import { VendedorService } from '../providers/vendedor.service';
import { LoginService } from '../providers/login.service';
import { MapGenericService} from '../providers/map-generic.service';
import { MapService } from '../providers/map.service';
import { SupervisoresProvider } from '../providers/supervisores.service';
import { LocationService } from '../providers/location';

import { BackgroundGeolocation } from '@ionic-native/background-geolocation';
import { MapGenericSupService } from '../providers/map-generic-sup';

const configFirebase = {
  apiKey: 'AIzaSyAJDgpY9ssAyjIX_vA7S1D_fgcA26BBvxQ',
  authDomain: 'svmmoviltest.firebaseapp.com',
  databaseURL: 'https://svmmoviltest.firebaseio.com',
  projectId: 'svmmoviltest',
  storageBucket: 'svmmoviltest.appspot.com',
  messagingSenderId: '1094384829553'
};

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/home-jefe-ventas/home-jefe-ventas.module.ngfactory#HomeJefeVentasPageModuleNgFactory', name: 'HomeJefeVentasPage', segment: 'home-jefe-ventas', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module.ngfactory#HomePageModuleNgFactory', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/info/info.module.ngfactory#InfoPageModuleNgFactory', name: 'InfoPage', segment: 'info', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/list-supervidores/list-supervidores.module.ngfactory#ListSupervidoresPageModuleNgFactory', name: 'ListSupervidoresPage', segment: 'list-supervidores', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module.ngfactory#LoginPageModuleNgFactory', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/map-generic/map-generic.module.ngfactory#MapGenericPageModuleNgFactory', name: 'MapGenericPage', segment: 'map-generic', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/map-prevendedores/map-prevendedores.module.ngfactory#MapPrevendedoresPageModuleNgFactory', name: 'MapPrevendedoresPage', segment: 'map-prevendedores', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/map-generic-sup/map-generic-sup.module.ngfactory#MapGenericSupPageModuleNgFactory', name: 'MapGenericSupPage', segment: 'map-generic-sup', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/map-supervisor/map-supervisor.module.ngfactory#MapSupervisorPageModuleNgFactory', name: 'MapSupervisorPage', segment: 'map-supervisor', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/map/map.module.ngfactory#MapPageModuleNgFactory', name: 'map', segment: 'map/:key', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/preventa/preventa.module.ngfactory#PreventaPageModuleNgFactory', name: 'PreventaPage', segment: 'preventa', priority: 'low', defaultHistory: [] }
  ]
}),
    AngularFireModule.initializeApp( configFirebase ),
    AngularFireDatabaseModule,
    IonicStorageModule.forRoot({
      name: '__mydb',
         driverOrder: ['indexeddb', 'sqlite', 'websql']
    })
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    Sim,
    StatusBar,
    SplashScreen,
    Geolocation,
    GoogleMaps,
    SQLite,
    BackgroundGeolocation,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    VendedorService,
    LoginService,
    Network,
    MapGenericService,
    MapService,
    SupervisoresProvider,
    LocationService,
    MapGenericSupService
  ]
})
export class AppModule {}



